body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  /* holographic component */
  --transparent-dark-color: rgba(0, 0, 0, 0.4);
  --dark-shadox:5px 5px 22px 0px rgba(0,0,0,0.19);
  --shine-tilt: 60%;
  --holographicBkg-width: 400px;
  --holographicBkg-height: auto;
  --light-holo-color: #cfdfdc3d;
  --dark-holor-color: #8ed5c9;
  --shine-color: 255, 255, 255;
  --repeating-gradient: repeating-linear-gradient(
    -45deg,
    transparent 0 30%,
    rgba(var(--shine-color), 1) 30% 50%
  );
  --shadow: rgba(0, 0, 0, 0.2);
  --glow: -2px 0 30px 20px;
  --back-distance: -1rem;
  /* nav */
  --nav-font-size: 1em;
  --nav-padding: calc(var(--nav-font-size) * 1.5);
  --side-bar-pad: 4rem 2rem;

  --largeBreakpoint: 1000px;

  --yellow-color: #f0b90b;
  --main-bkg-color: #1A132B;
  --main-bkg-color-lighter:  #392e53;
  --main-bkg-color-darker:  #0e0a16;
  --patternBg: url("https://www.transparenttextures.com/patterns/stardust.png");
  --minimum-height: 650px;
  --nav-big-btn-color: #f14646;
  --nav-big-button-color-hover: #f85f5f;
  /* event */
  --event-color: white;
  --accent: #004BA8;
  --disabled: #282828; 
  /* --almost-white: #d6d6d6; */
    --almost-white: white;
}
.second-schema {
  --yellow-color: #03e1ff;
  --nav-big-btn-color: #dc1fff;
  --nav-big-button-color-hover: #e25bfa;
  --main-bkg-color:  #1c1c1c;
  --event-color: black;
  --light-holo-color: #7cf0ff2a;
  --dark-holor-color: #a6f0fac7;
  --accent: #FF838c;
  --disabled: #d36d74; 
}
.App {
  text-align: center;
}

html {
  background-color: #1A132B;
  background-color: var(--main-bkg-color);
  background-image: url("https://www.transparenttextures.com/patterns/stardust.png");
  background-image: var(--patternBg);
  -webkit-animation: zoomIn 2s ease;
          animation: zoomIn 2s ease;
  background-size: 30%;
  scroll-behavior: smooth;
}

html, body, #root {
  max-width: 100vw;
  overflow-x: hidden;
}

@-webkit-keyframes zoomIn {
  from {
    background-size: 26%;
  }

  to {
    background-size: 30%;
  }
}

@keyframes zoomIn {
