.container {
  width: 100%;
  margin: auto;
  font-family: 'Lexend', sans-serif;
  font-weight: 300; 
}

.accordion {
  height: 60px;
  overflow-y: hidden;
  position: relative;
  margin-bottom: 30px;
  padding-left: 0;
  background: rgba(0,0,0,0.3);
  border: 1px solid var(--main-bkg-color-lighter);
  padding: 1em;
  border-radius: 10px;
}

.accordionOpen {
  height: auto;
  padding-top: 40px;
}

.accordionOpen .question {
  top: 29px;
}

.dropdownButton {
  position: absolute;
  right: 10px;
  top: 13px;
  transition: all ease 0.3s;
}

.dropdownButton.active {
  transform: rotate(180deg);
}

.question {
  color: var(--accent);
  display: block;
  font-style: italic;
  font-size: 13pt;
  width: 90%;
  cursor: pointer;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 20px; 
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  overflow-x: hidden;
}

.answer {
  width: 90%;
  margin-left: 30px;
  margin-top: 20px;
  opacity: 0;
}

.accordionOpen .answer{
  opacity: 1;
}

@media (max-width: 1200px) {
  .container {
    width: 100%;
  }
}
