.nav {
  z-index: 1000;
  margin: 0;
  font-style: italic;
  width: 100%;
  margin: auto;
  background-color: rgba(0, 0, 0, 0);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  gap: 2%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  list-style: none;
  position: fixed;
  font-size: var(--nav-font-size);
  padding: 0;
  top: 0;
  left: 0;
  white-space: nowrap;
  padding: 10px 15%;
  -webkit-transition: all ease 0.5s;
  transition: all ease 0.5s;
}

.nav.visible {
  box-shadow: 0px 13px 26px -10px rgba(0, 0, 0, 0.29);
  -webkit-box-shadow: 0px 13px 26px -10px rgba(0, 0, 0, 0.29);
  -moz-box-shadow: 0px 13px 26px -10px rgba(0, 0, 0, 0.29);
  background-color: var(--main-bkg-color-darker);
}

.connectWallet {
  background-color: rgba(0, 0, 0, 0);
  color: white;
  -webkit-transition: all ease 0.3s;
  transition: all ease 0.3s;
  font-style: italic;
  padding: 3px 10px;
  height: 30px;
  font-family: inherit;
  margin-right: 0;
  cursor: pointer;
  border: 1px solid var(--main-bkg-color-lighter);
  text-transform: lowercase;
  border-radius: 10px;
}

.connectWallet:hover {
  background-color: var(--accent);
}

.connectWallet img {
  width: 20px;
  height: 20px;
  overflow: hidden;
}

.walletContainer {
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border: 1px solid var(--main-bkg-color-lighter);
  padding: 3px 5px;
  border-radius: 10px;
}

.walletIcon {
  max-width: 20px;
  max-height: 20px;
  overflow: hidden;
  margin-right: 15px;
}

.nav .wrapImage {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  /* top: calc(-1 * (var(--nav-font-size))); */
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 20pt;
}

.nav .wrapImage img {
  width: 60px;
  height: auto;
  -webkit-transition: all ease 0.3s;
  transition: all ease 0.3s;
}

.nav .wrapImage img:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.nav a {
  text-decoration: none;
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  cursor: pointer;
  -webkit-transition: all ease 0.2s;
  transition: all ease 0.2s;
}

.nav a.selectedLink {
  color: var(--accent) !important;
}

.nav a:hover:not(.navBigBtn) {
  color: var(--accent);
}

.navBigBtn {
  background-color: var(--accent);
  border: none;
  color: white;
  font-size: var(--nav-font-size);
  font-family: inherit;
  cursor: pointer;
  border-radius: 3px;
  padding: 0;
  position: relative;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0.5em 2em;
  isolation: isolate;
}

.navBigBtn.disabled {
  background-color: var(--disabled);
  font-family: monospace !important;
}

.navBigBtn div {
  width: 100%;
  height: 100%;
  background-color: var(--accent);
  color: white;
  -webkit-filter: brightness(80%);
          filter: brightness(80%);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  -webkit-clip-path: ellipse(0 0 at 50% 100%);
          clip-path: ellipse(0 0 at 50% 100%);
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.navBigBtn:hover div {
  -webkit-clip-path: ellipse(120% 120% at 50% 100%);
          clip-path: ellipse(120% 120% at 50% 100%);
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.wrapText {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 5%;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  /* justify-content: space-between; */
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: var(--nav-padding);
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  position: relative;
}

.nav i {
  color: white;
  font-size: 2rem;
  cursor: pointer;
}

.sideBar {
  max-width: 100vw;
  z-index: -1;
  width: 30rem;
  padding: var(--side-bar-pad);
  background: var(--main-bkg-color);
  color: white;
  height: 100%;
  position: fixed;
  right: 0;
  top: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: calc(1.3rem + 0.7vw);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  overflow: hidden;
  gap: 5%;
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
  min-height: var(--minimum-height);
  -webkit-box-shadow: -10px 13px 26px -10px rgba(0, 0, 0, 0.29);
          box-shadow: -10px 13px 26px -10px rgba(0, 0, 0, 0.29);
}

.sideBar li {
  display: block;
  margin: 1em 0;
}

.sideBar button {
  font-size: inherit;
  padding: 1rem 2rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-top: 1em;
  margin: auto;
}

@media (max-width: 1040px) {
  :root {
    --nav-font-size: 0.8rem;
  }
  .navBigBtn {
    padding: 0.75em 1.5em;
  }
  .nav {
    padding: 0 5%;
  }
}
