.traitsContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 95%;
  margin: auto;
  text-align: center;
  padding: 2em;
  border-radius: 1em;
  background: var(--main-bkg-color-lighter);
  color: var(--almost-white);
  margin-top: 50px;
  background: rgba(0,0,0,0.3);
  border: 1px solid var(--main-bkg-color-lighter);
  padding: 1em;
  border-radius: 10px;
}

.traitsContainer .header {
  font-family: 'Lexend', sans-serif;
  font-weight: 700; 
  margin: 0;
}

.body {
  width: 85%;
  max-width: 800px;
}