.mainPageInfo {
  width: 100%;
  max-width: 26em;
  display: flex;
  flex-direction: column;
  align-items: center;
  filter: drop-shadow(8px 8px 10px var(--main-bkg-color-darker)) !important;
  border-radius: 10px;
  margin-right: 2em;
}

.containMain {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  overflow: hidden;
}

.gif {
  width: 100%;
  height: auto;
}

.blur {
    filter: blur(10px);
}

@media (max-width: 1040px) {
  .mainPageInfo {
    max-width: 40%;
  }
}

@media (max-width: 768px) {
  .mainPageInfo {
    margin-right: 0;
    max-width: 60%;
  }
  .containMain {
    margin-right: 0;
  }
}

@media (max-width: 480px) {
  .mainPageInfo {
    max-width: 80%;
  }
}