.socialBar {
  z-index: 0;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.socialBar > a {
  text-align: center;
  text-decoration: none;
  color: white;
  margin: 0 15px;
  height: 15px;
  width: 15px;
  display: flex;
  align-items: center;
}

.socialBar > a:first-child {
  margin-left: 0;
}

.socialBar > a:last-child {
  margin-right: 0;
}

.socialBar > a img {
  width: 100%;
  height: auto;
  object-fit: contain;
  transition: 0.3s;
}

.socialBar > a > svg {
  fill: white;
  transition: 0.3s;
}

.socialBar > a svg:hover {
  transform: scale(1.2);
}
