.cheatSheetWrapper {
  color: white;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  animation: fadeIn 1.5s ease;
  overflow:hidden;
  margin-top: 30px;
}

.traitsWrapper {
  display: flex;
  justify-content: space-evenly;
  padding: 0 2em;
  font-family: 'Lexend', sans-serif;
  font-weight: 300; 
}

.traitsContainer {
  max-width: 300px;
  padding: 1em;
  border-radius: 0.5em;
  margin-bottom: 2em;
  color: white;
  border: 2px solid var(--main-bkg-color-lighter);
  height: 63px;
  overflow: hidden;
  cursor: pointer;
}

.accordionOpen {
  height: auto;
}

.traitsContainer.superRare {
  border: 2px solid var(--accent);
  color: var(--accent);
  box-shadow: 0 0 24px var(--accent);
}

.header {
  text-align: center;
  color: white;
  font-family: 'Lexend', sans-serif;
  font-weight: 600; 
}

.trait {
  font-weight: 700;
  text-transform: capitalize;
  font-size: 1.2rem;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: auto;
  color: var(--accent);
  white-space: nowrap;
}

.item {
  text-align: left;
}

.dropdownButton {
  bottom: -0.75em;
  transition: all ease 0.3s;
}

.dropdownButton.active {
  transform: rotate(180deg);
  bottom: 0.75em;
}

.rarity {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 10em;
  margin-right: 1em;
}

.rarity > div {
 font-weight: 500;
}

.accordionOpen> ul {
  padding-left: 1.5em;
}

.arrowContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 1em;
  margin-bottom: 1em;
}

.arrow {
  width: 2px;
  height: 15vh;
  background: white;
}

.arrowHead {
  margin-top: -0.55em;
}

.myMasonryGrid {
  display: -webkit-box; /* Not needed if autoprefixing */
  display: -ms-flexbox; /* Not needed if autoprefixing */
  display: flex;
  margin-left: -30px; /* gutter size offset */
  width: auto;
}
.myMasonryGridColumn {
  padding-left: 30px; /* gutter size */
  background-clip: padding-box;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@media (max-width: 900px) {
  .header {
    font-size: 1.8rem;
  }
}

@media (max-width: 540px) {
  .header {
    font-size: 1.4rem;
  }

  .rarity {
    font-size: 0.75rem;
  }
}

