.teamContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.teams {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 3em;
  justify-content: center;
}

.team {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.avatar {
  max-width: 250px;
  border-radius: 10px;
  filter: drop-shadow(8px 8px 10px var(--main-bkg-color));
  transition: transform 0.1s ease;
  transform-style: preserve-3d;
  position: relative;
  will-change: transform;
}

.url {
  border: 1px solid var(--main-bkg-color-lighter);
  padding: 0.25em 3em;
  border-radius: 10px;
  margin-top: 1em;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 250ms;
}

.url:hover {
  transform: scale(0.97);
}

.url > svg {
  fill: white;
  transition: 0.3s;
  width: 1em;
  height: 1em;
}

.details {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.role,
.name {
  width: auto;
  margin-bottom: 0;
}

@media (max-width: 900px){
  .teams {
    grid-template-columns: auto;

  }

  .team {
    flex-direction: row;
    justify-content: flex-start;
  }

  .avatar {
    max-width: 50%;
    margin-bottom: 0;
    margin-right: 2em;
  }

  .details {
    align-items: flex-start;
  }
}

@media (max-width: 480px){
  .name {
    font-size: 1rem;
  }
}