h1 {
  margin-bottom: 50px;
  font-style: italic;
  text-align: center;
  font-family: 'Lexend', sans-serif;
  font-weight: 600; 
}

.roadmapContainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  margin: auto;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
          font-family: 'Lexend', sans-serif;
          font-weight: 300;
        
}

.roadmapLeft {
  position: relative;
  flex: 2;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 20px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.roadmapRight {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-top: 35px;
}

.roadmapRight .sampleApe {
  max-width: 200px;
  margin: 5px;
  border-radius: 10px;
  display: flex;
  -webkit-filter: drop-shadow(8px 8px 10px var(--main-bkg-color));
          filter: drop-shadow(8px 8px 10px var(--main-bkg-color));
  -webkit-transition: -webkit-transform 0.1s ease;
  transition: -webkit-transform 0.1s ease;
  transition: transform 0.1s ease;
  transition: transform 0.1s ease, -webkit-transform 0.1s ease;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  position: relative;
  will-change: transform;
}



.roadmapItem {
  padding-left: 0;
  cursor: pointer;
  border-radius: 5px;
  color: var(--almost-white);
  position: relative;
  height: 55px;
  overflow-y: hidden;
  position: relative;
  margin-bottom: 15px;
  background: rgba(0,0,0,0.3);
  border: 1px solid var(--main-bkg-color-lighter);
  padding: 1em;
  border-radius: 10px;
  padding-top: 35px;
}

.roadmapItemOpen{
  height: auto;
}

.dropdownButton {
  position: absolute;
  right: 10px;
  top: 13px;
  transition: all ease 0.3s;
}

.dropdownButton.active {
  transform: rotate(180deg);
}

.roadmapItem p {
  margin-left: 10px;
}

.roadmapItem .roadmapDescription {
  width: 85%;
  margin-top: 20px;
  margin-left: 30px;
  opacity: 0;
}

.roadmapItemOpen .roadmapDescription{
  opacity: 1;
}

.roadmapItem .roadmapTitle {
  color: var(--accent);
  font-weight: bold;
  font-style: italic;
  width: 85%;
  display: block;
  position: absolute;
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
}

.roadmapItemOpen .roadmapTitle {
  top: 26px;
}

.roadmapText {
  margin: 1.5em auto 1em auto;
  width: 100%;
  color: var(--almost-white);
}

.roadmapBtnContainer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.roadmapBtn {
  width: 100%;
  display: inline-block;
  background: var(--main-bkg-color-lighter);
  color: white;
  transition: all ease 0.3s; 
  font-style: italic;
  padding: 10px 30px;
  font-family: inherit;
  margin: 0;
  cursor: pointer;
  border: 1px solid var(--main-bkg-color-lighter);
  text-transform: lowercase;
  border-radius: 10px;
}

.roadmapBtn:hover {
  background-color: var(--accent);
}

.roadmapBtnContainer > a {
  display: block;
  text-decoration: none;
  color: white;
  width: 100%;
  margin: 0;
  padding: 0;
}


.link {
  width: auto;
  color: rgb(6, 210, 247);
  margin-left: 1em;
  font-size: 0.85rem;
}

.header {
  margin-bottom: 0.5em;
}

@media (max-width: 1500px) {
  .container {
    width: 100%;
  }
}

@media (max-width: 1200px) {
  .container {
    width: 80%;
  }
  .divider {
    display: none;
  }
  .roadmapContainer {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .roadmapItem {
    width: 100%;
  }
  .roadmapRight {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    overflow-x: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
}

@media (max-width: 900px) {
  .roadmapRight {
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
  }
  .roadmapText {
    padding: 0;
  }
}


